.Pagination{
  display: flex;
  list-style: none;
  justify-content: flex-end;
  padding: 24px 20px;
  font-size: 16px;
  margin: 0;
}

.Pagination-item{
  display: block;
  padding: 2px 4px 2px 2px;
  margin: 0;
  cursor: pointer;
  height: 20px;
  min-width: 20px;
  box-sizing: border-box;
  text-align: center;
  line-height: 18px;
  &:hover{
    background: #E1E1E1;
  }
}

.Pagination-item_active{
  &:hover{
    background: #0087E9;
  color: #fff;
  }
  background: #0087E9;
  color: #fff;
}

.Pagination-item_split{
  &:hover{
    background: transparent;
    cursor: default;
    color: #CCCCCC;
  }
  background: transparent;
    cursor: default;
    color: #CCCCCC;
}
